import request from '@/utils/request'
export default {
    findProposalWhereTotal(arg) {//
        console.log(" begin")
        console.log(JSON.stringify(arg))
        console.log(" end")
        return request({
        url:`/selectbaseinfo/findProposalWhereTotal`,
        method: 'post',
        data:JSON.stringify(arg)
        })
   },
    //
    findProposalWhere(arg) {
        console.log("begin")
        console.log(JSON.stringify(arg))
        console.log("end")
        return request({
        url:`/selectbaseinfo/findProposalWhere`,
        method: 'post',
        data:JSON.stringify(arg)
        })
   },
   findPolicyWhereTotal(arg) {//
        console.log(" begin")
        console.log(JSON.stringify(arg))
        console.log(" end")
        return request({
            url:`/selectbaseinfo/findPolicyWhereTotal`,
            method: 'post',
            data:JSON.stringify(arg)
        })
    },
    findPolicyWhere(arg){//
        console.log(" begin")
        console.log(JSON.stringify(arg))
        console.log(" end")
        return request({
            url:`/selectbaseinfo/findPolicyWhere`,
            method: 'post',
            data:JSON.stringify(arg)
        })
    },
    findProposalInfo(ProposalNo){//
        console.log(ProposalNo)
        return request({
            url:`/selectbaseinfo/findProposalInfo`,
            method: 'get',
            params:{
                ProposalNo,
                time: new Date().getTime()
            }
        })    
    },
    findPolicyInfo(PolicyNo){//-
        return request({
            url:`/selectbaseinfo/findPolicyInfo`,
            method: 'get',
            params:{
                PolicyNo,
                time: new Date().getTime()
            }
        })
    }    
} 