import request from '@/utils/request'
export default {//
    getexchangerage(mainCurrency,sumCurrency,payCurrency,exchangeDate) {
        let url=''
        if(process.env.NODE_ENV=="production"){
            url=`/selectbaseinfo/getexchangerage`
        }else{ 
            url='/getexchangerage'
        }
        return request({
        url,
        method: 'get',
        params:{
            mainCurrency,//
            sumCurrency,//
            payCurrency,//
            exchangeDate
            }
        })
   }

} 