class  insuredInfoVo{
	constructor(){
			this.serialNo=''
			this.insuredType='3'
			this.customerCode=''
			this.customerCName=''
			this.identifyType='01'
			this.identifyNo=''
			this.mobile=''
			this.linkName=''
			this.phoneNumber=''
			this.address1=''
			this.address2=''
			this.address3=''
			this.address1Name=''
			this.address2Name=''
			this.address3Name=''
			this.address4=''
			this.insuredAddress=''
			this.postCode=''
			this.urgentPhone=''
			this.flag=''//
		}
	}
  const IdentifyTypeArray=[
		{id:'',name:''},
		{id:'01',name:''},
		{id:'02',name:''},
		{id:'04',name:''},
		{id:'05',name:''},
		{id:'07',name:''},
		{id:'09',name:''},
		{id:'10',name:''},
		{id:'51',name:''},
		{id:'54',name:''},
		{id:'55',name:''},
		{id:'71',name:''},
		{id:'73',name:''},
		{id:'74',name:''},
		{id:'99',name:''},
	]
     export default  ({
          name:'Insured',
          data(){
            return{
			  imgUrl:require('@/assets/images/markMustInput.jpg'),
              insuredInfoList:[],//
              //totalPage : [], 
              pageNum : 1,    
              insuredShow:new insuredInfoVo(), 
              currentPage : 0,
              isShow:false,
			  GoPage:1,
			  IdentifyTypeList:IdentifyTypeArray,//
			  addressInfoVoList:[],//
			  address2InfoVoList:[],//
			  address3InfoVoList:[],//
              } 
          },
          methods:{
			show(){ this.isShow =!this.isShow;},
			initOpitons(data){// select options
                this.addressInfoVoList=data.addressInfoVoList;//
			},
			initInsuredData(data){
				for(let item of data.insuredInfoVos){
					this.insuredInfoList.push(item)
				}
				this.insuredShow=this.insuredInfoList[this.currentPage]
				this.pageNum=this.insuredInfoList.length
				console.log(`this.pagenum${this.pageNum}`)
			},
            // 
            nextPage() {
              if (this.currentPage === this.pageNum - 1) return ;
              this.insuredShow = this.insuredInfoList[++this.currentPage];
            },
            // 
            prePage() {
              if (this.currentPage === 0) return ;
              this.insuredShow = this.insuredInfoList[--this.currentPage];
            },
            // 
            page(i){
				if(i=="0"||i>this.pageNum){
					return false;
				}
                this.insuredShow = this.insuredInfoList[i-1];
				this.currentPage = --i
            },
			checkIdentifyNumber(){}, 
			//checkIdentifyNumber 
       }
    })