<template>
  <table class="common" cellpadding="3" cellspacing="0">
    <tr>
      <td class="common" style="text-align:left">
        <img name="MainReinsCiImg" style="cursor:hand" src="@/assets/images/butCollapse.gif"
          @click="show()">
        <img src="@/assets/images/markMustInput.jpg"><br>
        <table id="MainReinsCi" class="common" style="" border="0" v-show="MainReinsCiShow">
          <thead>
            <tr>
              <td class="centertitle" style="width:6%">(offered)%</td>
              <td class="centertitle" style="width:6%">(signed)%</td>
              <td class="centertitle" style="width:8%"></td>
              <td class="centertitle" style="width:8%"></td>
              <td class="centertitle" style="width:8%"></td>
              <td class="centertitle" style="width:6%">%</td>
              <td class="centertitle" style="width:8%"></td>
              <td class="centertitle" style="width:6%">%</td>
              <td class="centertitle" style="width:8%"></td>
              <td class="centertitle" style="width:6%">%</td>
              <td class="centertitle" style="width:6%"></td>
              <td class="centertitle" style="width:8%"></td>
              <td class="centertitle" style="width:8%"></td>
            </tr>
          </thead>
          <tbody>
                <tr  ref="mainReinsCiData">
                        <input type="hidden" name="MainReinsCi_Flag">
                        <input type="hidden" name="StartDateMain">
                        <input type="hidden" name="EndDateMain">
                    <td>
                        <input type="text" name="offeredLine" ref="offeredLine" class="common" style="width:100%"  maxlength="9" description="offered" v-model='ReinsCededInfo.offeredLine' @blur="offeredRate();inDisPremium1();">
                    </td>
                    <td>
                        <input type="text" name="signedLine"  ref="signedLine" class="common" style="width:100%"  maxlength="9" description="signed" v-model='ReinsCededInfo.signedLine' @blur="offeredRate();">
                    </td>
                    <td>
                        <input type="text" name="inAmount"  ref="inAmount" class="common" style="width:100%"  maxlength="18" description=""  v-model='ReinsCededInfo.inAmount'
                        @blur="$uiCommon.numberFormatMoney($event);$uiCommon.checkDecimal1($event,17,2,'','',',',3);">
                    </td>
                    <td>
                        <input type="text" name="noHaveTaxFee"  ref="noHaveTaxFee" class="common" style="width:100%"  maxlength="18" description=""  v-model='ReinsCededInfo.noHaveTaxFee'
                           @change="taxFeeRate();operateFeeRate();agentSignedFeeRate();inDisPremium1();$uiCommon.checkDecimal1($event,17,2,'','',',',3);$uiCommon.numberFormatMoney($event);">
                        <input type="hidden" name="inPremium"  ref="inPremium" class="common" style="width:100%"  maxlength="18" description=""   v-model='ReinsCededInfo.inPremium'
                            @blur="inDisPremium1();$uiCommon.checkDecimal1($event,17,2,'','',',',3);">
                    </td>
                    <td> 
                        <select  name="exchangeFlag"   ref="exchangeFlag" class="common" style="width:100%" v-model='ReinsCededInfo.exchangeFlag'>
                          <option value='1'></option>
                        </select>                   
                    </td>
                    <td>
                        <input type="text" name="commSignedLine"   ref="commSignedLine"  class="common" style="width:100%"  maxlength="9" description="%" 
                         v-model='ReinsCededInfo.commSignedLine'
                         @blur="agentSignedFeeRate();inDisPremium1();checkRate($event);">
                        
                    </td>
                    <td>
                    <input type="text" name="reinsCiCharges"  ref="reinsCiCharges" class="common" style="width:100%"  maxlength="18" description="" 
                         @blur="inDisPremium1();$uiCommon.checkDecimal1($event,17,2,'','',',',3);$uiCommon.numberFormatMoney($event);"  v-model='ReinsCededInfo.reinsCiCharges'>
                        </td>
                    <td>
                        <input type="text" name="billsFeeRate" ref="billsFeeRate" class="common" style="width:100%"  maxlength="9" description="" 
                             v-model='ReinsCededInfo.billsFeeRate'
                            @blur="operateFeeRate();inDisPremium1();checkRate($event);">
                    </td>
                    <td>
                        <input type="text" name="billsFee"  ref="billsFee"  class="common" style="width:100%"  maxlength="18" description="" 
                        v-model='ReinsCededInfo.billsFee' @blur="inDisPremium1();$uiCommon.checkDecimal1($event,17,2,'','',',',3);$uiCommon.numberFormatMoney($event);" >
                        </td>
                        <td>
                        <input type="hidden" name="taxRate"  ref="taxRate"  class="common" style="width:100%"  maxlength="9" description="" 
                            v-model="ReinsCededInfo.taxRate"
                            @blur="taxFeeRate();inDisPremium1();checkRate($event);">
                        <input type="text" name="addedTaxRate" ref="addedTaxRate" class="common" style="width:100%"  maxlength="9" description="" 
                             v-model="ReinsCededInfo.addedTaxRate"
                            @change="calculateAddexTaxFee();taxFeeRate();operateFeeRate();agentSignedFeeRate();inDisPremium1();checkRate($event);">
                      
                        </td>
                        <td>
                        <input type="hidden" name="tax"  ref="tax" class="common" style="width:100%"  maxlength="18" description="" 
                            @blur="inDisPremium1();$uiCommon.checkDecimal1($event,17,2,'','',',',3);" v-model='ReinsCededInfo.tax'>
                        <input type="text" name="addedTaxFee"  ref="addedTaxFee" class="common" style="width:100%" maxlength="18" description=""
                         v-model='ReinsCededInfo.addedTaxFee' 
                            @blur="inDisPremium1();$uiCommon.checkDecimal1($event,17,2,'','',',',3);$uiCommon.numberFormatMoney($event);">
                        </td>
                        <td>
                        <input type="text" name="inDisPremium" ref="inDisPremium" class="common" style="width:100%"  maxlength="18" description="" 
                            v-model="ReinsCededInfo.inDisPremium"
                            @blur="inDisPremium1();$uiCommon.checkDecimal1($event,17,2,'','',',',3);">
                    </td>
                    <td>
                        <select class="common" style="width: 100%" name="isNeedEstimate" ref="isNeedEstimate" description=""  v-model="ReinsCededInfo.isNeedEstimate">
                            <option value=1></option> 
                            <option selected value=0></option>
                        </select>
                    </td>
                </tr>
          </tbody>
        </table>
      </td>
    </tr>
  </table>
</template>
<script>
let moduel=require(`@/pubJs/${global.path}/MainReinsCiInput${global.show}.js`)
let MainReinsCiInput=moduel.default
export default {
  ...MainReinsCiInput
}

</script>