import request from '@/utils/request'
export default {//   
    getselectaddressinfo(upperCode) {
        let url;
        if(process.env.NODE_ENV=="production"){
            url=`/selectbaseinfo/getselectaddressinfo`
            //`selectbaseinfo/getselectaddressinfo`
        }else{ 
            url='/getselectaddressinfo'
        }
        return request({
        url,
        method: 'get',
        params:{
            upperCode,//
            
            }
        })
   }

} 