									<td>
										<input type="text" name="commissionPercentReal"  ref='commissionPercentReal' class="common3" readonly style="width:100%" description=""   :value="Agent.commissionPercentReal">      
									</td>
									<td>
										<input type="text" name="commissionAmount"  ref='commissionAmount'  class="common3" readonly style="width:100%" description="" :value="Agent.commissionAmount">
