					<td class="title">%</td>
					<td>
						<input type="text" name="SelfRate1" class="readonly3" readonly description="">
					</td>
				</tr>
				<tr id="trDisRate1" style="display:none">
					<td class="title">%</td>
					<td class="mei">
						<input type="text" name="DisRate1" class="common3" maxlength="9" description="">
					</td>
					<td class="title"></td>
					<td class="mei">
						<input type="text" name="PremiumDisRate1" class="readonly3" readonly description="">
					</td>
				</tr>
				<tr id="trChangePremiumDisRate1" style="display:none">
					<td class="title"></td>
					<td class="mei">
						<input type="text" name="ChangePremiumDisRate1" class="readonly3" readonly description="">
					</td>
					<td class="mei"></td>
					<td class="mei"></td>
				</tr>
			</table>
