				<tr id="trAll" > 
					<td colspan="4">
						<table id="AllPerformance" class=list cellpadding="3" cellspacing="1" >
							<thead>
								<tr>
									<td class="centertitle" style="width:10%"></td>
									<td class="centertitle" style="width:20%"></td>
									<td class="centertitle" style="width:20%"></td>
									<td class="centertitle" style="width:10%">%</td>
									<td class="centertitle" style="width:10%"></td>
									<td class="centertitle" style="width:15%"></td>
								</tr>
							</thead>
							<tbody>
								<tr  ref='Performance' class="common"  v-for="(Performance,index) in AllPerformance_Data" :key="index">
