									<input type="hidden" name="AllPerformance_Flag" description=""  :value="Performance.AllPerformance_Flag">
									<td><input type="text" name="PerformanceNo" class="common3" style="width:100%;" description=""  	   readonly   :value="Performance.PerformanceNo"></td>
									<td><input type="text" name="EveryUserCode" class="common3" style="width:100%;" description="" 	readonly   :value="Performance.EveryUserCode"></td>
									<td><input type="text" name="EveryUserName" class="common3" style="width:100%" description=""   	readonly   :value="Performance.EveryUserName"></td>
									<td><input type="text"  name="PerformancePercent" ref="PerformancePercent" :class="Performance.AllPerformance_Flag=='U'?'common3u':'common3'" style="width:100%" description="%" readonly  :value="Performance.PerformancePercent"></td>
									<td><input type="text" name="PerformanceCurrency" ref="PerformanceCurrency" class="common3"  style="width:100%" description="" 	readonly  :value="Performance.PerformanceCurrency"></td>
									<td><input type="text" name="PerformanceFee"  	  ref="PerformanceFee" :class="Performance.AllPerformance_Flag=='U'?'common3u':'common3'"   style="width:100%" description=""   	readonly  :value="Performance.PerformanceFee"></td>
								</tr>          
							</tbody>
							<tfoot>
								<tr class="common"  >
									<td id="tdbutton_Insert" colspan="7"></td>
