import  Address from '@/api/getselectaddressinfo.js'
   export default {
        name:'AppliInsured',
        data(){
          return{
            isShow:false,
            riskCode:this.$store.state.riskCode==''?'2244':this.$store.state.riskCode,
			appliInfoVo:{
				customerCode:'',
				customerCName:'',
				identifyType:'74',
				identifyNo:'',
				datevalid:'',
				insuredType:'2',
				mobile:'',
				linkName:'',
				phoneNumber:'',
				address1:'',
				address2:'',
				address3:'',
				address1Name:'',
				address2Name:'',
				address3Name:'',
				address4:'',
				insuredAddress:'',
				postCode:'',
				email:'',
				businessSort:'',
				businessDetail:'',
				qualification:'',
				relationInsured:'',
				capitalauThority:'',
				doBesinessIncome:'',
				customerLevel:'',
				insuredRemark:'',
				invoiceMan:'',
				flag:'',
				
			},
			businessSortList:[],//
            addressInfoVoList:[],//
			address2InfoVoList:[],//
			address3InfoVoList:[],//
            IdentifyTypeList:[
					{id:'',name:''},
					{id:'01',name:''},
					{id:'02',name:''},
					{id:'04',name:''},
					{id:'05',name:''},
					{id:'07',name:''},
					{id:'09',name:''},
					{id:'10',name:''},
					{id:'51',name:''},
					{id:'54',name:''},
					{id:'55',name:''},
					{id:'71',name:''},
					{id:'73',name:''},
					{id:'74',name:''},
					{id:'99',name:''},
			],
			imgUrl:require('@/assets/images/markMustInput.jpg'),
			showImgUrl:require('@/assets/images/butCollapse.gif'),
          }
        }, 
		created(){},
		directives:{
			'mydirective':{
				// eslint-disable-next-line no-unused-vars
				bind:function (el,binding){
						el.title=binding.value
			   },
			  
				update:function(el){//
				   if(el.title!=el.value){

                      el.style.backgroundColor="yellow"
				   }else{
					     el.style.backgroundColor=""
				   }
				},
			}
			
		  },	
        methods:{
            show() {this.isShow=!this.isShow;},
            initOpitons(data){
               this.businessSortList=data.businessSortList;//
               this.addressInfoVoList=data.addressInfoVoList;//
			},
			async initAppliInsuredData(data){
				//this.appliInfoVo.customerCName=data.appliInfoVo.customerCName
				for(let key in data.endorseDataVo.appliInfoVo){//JSON  
					if(key in this.appliInfoVo){
						this.appliInfoVo[key]=data.endorseDataVo.appliInfoVo[key]||''
					}
					if(this.$refs[key]){
						this.$refs[key].title=data.originDataVo.appliInfoVo[key]||''
					}
				}
				if(this.appliInfoVo.address1!=null&&this.appliInfoVo.address1!=''){
			    	await this.getselectaddressinfo(this.appliInfoVo.address1,'address1')
				}
				if(this.appliInfoVo.address2!=null&&this.appliInfoVo.address2!=''){
			    	await this.getselectaddressinfo(this.appliInfoVo.address2,'address2')
				}
				//this.getInsuredAddress()
				if(data.originDataVo.appliInfoVo.flag=="U"){
					for(let appliRef in this.appliInfoVo){
						if(this.$refs[appliRef]&&this.$refs[appliRef].title!=this.$refs[appliRef].value){
							this.$refs[appliRef].className='commonu'
						}
					}  
				}
					
			},
			getselectaddressinfo(arg,value){
				// eslint-disable-next-line no-unused-vars
				return new Promise((resolve,reject)=>{
					Address.getselectaddressinfo(arg).then((res)=>{
						if(res.status==200){
							let data=res.data.data
							if(value=="address1"){
								this.address2InfoVoList=data
								resolve()
							}else if(value=="address2"){
								this.address3InfoVoList=data
								resolve()
							}
						}  
					})
				})
			},
        },
  }
  