<template>
    <div>
  <table id="tbCoinsReinsCi" class="common" cellpadding="5" cellspacing="1">
    <tr>
      <td style="text-align:left">
        <img id="CoinsReinsCiImg" name="CoinsReinsCiImg"  style="cursor:hand" src="@/assets/images/butCollapse.gif" @click="showPage()"/>
        <img src="@/assets/images/markMustInput.jpg"><br>
        <table ref="coinsReinsCi" class="common"  v-show="CoinsReinsShow" border="0">
          <thead>
            <tr>
              <td class="centertitle" width="10%"></td>
              <td class="centertitle" width="20%"></td>
              <td class="centertitle" width="10%">%</td>
              <td class="centertitle" width="10%"></td>
              <td class="centertitle" width="15%">&nbsp;</td>
              <td class="centertitle" width="8%" v-if="$route.path!='/proposalShow'&&$route.path!='/policyShow'&&$route.path!='/endorseShow'" >&nbsp;</td>
            </tr>
          </thead>
          <tbody>
          <tr id="trCoinsReinsCi" v-for="(CoinsInfoVo, index) in coinsInfoVoList" :key="index">
            <td>
            	<input type="hidden" name="CoinsReinsCi_Flag" description="">
            	<input type="hidden" name="CoinsReinsCiSerialNo" description="">
              <input type="text" name="SerialNoDisplay" class="readonly3" readonly style="width:10%"  description=""  v-model='CoinsInfoVo.serialNo'>
							<input type="hidden" name="coinsType"  description="" v-model="CoinsInfoVo.coinsType">
							<input type="text" name="CoinsTypeDisplay" class="readonly3" readonly style="width:50%"  v-if='CoinsInfoVo.coinsType=="1"' value="" description="">
              <input type="text" name="CoinsTypeDisplay" class="readonly3" readonly style="width:50%"  v-else value="" description="">
            </td>
            <td>
              <select name="CoinsReinsCode" class="common"  v-model="CoinsInfoVo.coinsCode"  
                 @change="setcoinsName(index);calculateAddexTax(index);">
									<option value="" disabled></option>
                  <option :value="coinsCodeInfoVo.coinsCode" v-for="coinsCodeInfoVo,index in coinsCodeInfoVoList" :key="index" >{{coinsCodeInfoVo.coinsName}}</option>   
			          </select>
              <input type="hidden" name="coinsName" class="codename" style="width:100%" description=""  v-model="CoinsInfoVo.coinsName">
            </td>
            <td>
              <input type="text"  name="coinsRate" class="common3" style="width:100%" maxlength="9" description="" v-model="CoinsInfoVo.coinsRate"
              @change="$uiCommon.checkDecimal($event,8,5,'','');calculateInAmount(index);">
            </td>
            <td>
              <select name="ReinsFlag" class="common5" style="width:100%"  v-model="CoinsInfoVo.coinsFlag"   @change="getMainAddedTaxRate(index);">
               <option value="1" selected="selected" ></option>
               <option value="0"></option>
              </select>
            </td>
            <td>
            	<input type="button" name="button_AddCoinsReinsInfo_Insert"  class="longbutton" style="display:none;"
                        alt="" value="" @click="addCoinsReinsInfo()">
            </td>
            <td  v-if="$route.path!='/proposalShow'&&$route.path!='/policyShow'&&$route.path!='/endorseShow'" >
              <p align="right"><input type="button" name="button_CoinsReinsCi_Delete" class="button" alt="" value=" " 
                @click="deleteCoinsReinsCi(index)"></p>
            </td>
          </tr>
          </tbody>
             <tfoot>
            <tr v-if="$route.path!='/proposalShow'&&$route.path!='/policyShow'&&$route.path!='/endorseShow'" >
              <td colspan="6">
                <p align="right"><input type="button" name="button_CoinsReinsCi_Insert" class="button" alt="" value=" "
                  @click="insertCoinsReinsCi()"></p>
              </td>
            </tr>
          </tfoot>
        </table>
      </td>
    </tr>
  </table>
    </div>
</template>
<script>
let moduel=require(`@/pubJs/${global.path}/MainCoinsReinsCiInput${global.show}.js`)
let coinsReins=moduel.default
export default{
   ...coinsReins

}
</script>
