<template>
   <div  class="dialog" v-show="isAgriChildShow">
    <div class="content">
    <div  class="mainContent"> 
    <table id="SidyMainHead"  class="common"> 
      <tr>
      <td>
          <table  ref="SubSidy" class="common" border="0">
            <thead>
              <tr>
                <td class="centertitle" width="10%"></td>
                <td class="centertitle" width="27%"></td>
                <td class="centertitle" width="20%">(%)</td>
                <td class="centertitle" width="25%"></td>
                <td class="centertitle" width="13%"   v-if="$route.path!='/proposalShow'&&$route.path!='/policyShow'&&$route.path!='/endorseShow'" >&nbsp;</td>
              </tr>
            </thead>
            <tbody>
             <tr ref="subSidyData"  v-for="(SubSidyInfoVo, index) in subSidyInfoVoList" :key="index" >
              <td>
                <input type="hidden" name="flag" description="" :value='SubSidyInfoVo.flag'>
                <input type="text" name="serialNo" ref="serialNo" class="readonly" style="width:100%" description="" v-model="SubSidyInfoVo.c">
              </td>
              <td>
                <select name="subSidySort"  ref="subSidySort" class="common" style="width:100%" description=""   v-validate="'required'" v-model="SubSidyInfoVo.subSidySort" @change="checkSubSidySort(index)">
                  <option value="" disabled></option>
                  <option value="1"></option>
                  <option value="2"></option>
                  <option value="3"></option>
                  <option value="4"></option>
                  <option value="5"></option>
              </select>
              </td>
              <td>
                <input type="text" name="subSidyRate"  ref="subSidyRate"  class="common3" style="width:100%" description=""  maxlength="3"   v-validate="'required'"  v-model="SubSidyInfoVo.subSidyRate"
                  @keypress="$uiCommon.pressDecimal($event);"
                  @blur="countSubSidyPremium(index)">
              </td>
              <td>
                <input type="hidden" name="SubSidyCurrency" class="common3" value="CNY"
                    maxlength="3" description="" style="width:30px;display:none"
                    querytype="always" codetype="Currency1" coderelation="0,1" codelimit="must">
                <input type="hidden" name="SubSidyCurrencyName" value="" style="display:none" description="">
                <input type="text" name="subSidyPremium"  ref="subSidyPremium"  :class="SubSidyInfoVo.flag=='U'?'common3u':'common3'" style="width:99%" description=""  v-validate="'required'"   v-model="SubSidyInfoVo.subSidyPremium"
                @keypress="$uiCommon.pressDecimal($event)">
              </td>
              <td  v-if="$route.path!='/proposalShow'&&$route.path!='/policyShow'&&$route.path!='/endorseShow'" >
                <p align="right"><input type="button" name="button_SubSidy_Delete" class="button" alt="" value=" " @click="deleteSubSidy(index)"></p>
              </td>
            </tr>
            
            </tbody>
            <tfoot>
              <tr  v-if="$route.path!='/proposalShow'&&$route.path!='/policyShow'&&$route.path!='/endorseShow'"  >
                <td colspan="6">
                  <p align="right"><input type="button" name="button_SubSidy_Insert" class="button" alt="" value=" "
                    @click="insertSubSidy()"></p>
                </td>
              </tr>
            </tfoot>
          </table>
        </td>
      </tr>
    <table>
      <tr id="trAgriNature">
        <td>
          /{{AgriNature}}
        </td>
      </tr>
    </table>
      <table class="common" border="0">
          <tr>
          <td width="50%">
              <p align="center"><input type="button" name="buttonSubmit" class="button" alt="" value=" "
               @click="okCallback()"></p>
          </td>
          <td width="50%">
              <p align="center"><input type="button" name="buttonCancel" class="button" alt="" value=" "
              @click="cancelCallback()"></p>
          </td>
          </tr>
      </table>
      </table>
        </div>
    </div> 
</div>  
</template>
<script>
let moduel=require(`@/pubJs/${global.path}/MainAgriType${global.show}.js`)
let MainAgriType=moduel.default
//import  MainAgriType from '@/pubJs/MainAgriType.js'
export default{
    ...MainAgriType
  }
</script>
<style scoped>
    .mainContent{
        width: 100%;
        height: 100%;
        overflow-y: scroll;
        margin-bottom: 1%;
    }
    .dialog{
        background-color: rgba(0,0,0,0.8);
        position:fixed;
        top: 0px;
        left:0;
        right:0;
        bottom:0;
        width: 100%;
        height: 100%;
        padding:20px;
        font:bold 14px verdana,tahoma,helvetica;
        text-align:center;
    }
    .close.big {
        -webkit-transform: scale(2);
        -moz-transform: scale(2);
        -ms-transform: scale(2);
        -o-transform: scale(2);
        transform: scale(2);
    }
    .close{
        position: relative;
        display: inline-block;
        overflow: hidden;
        right: 20px;
        top:20px;
        color:#000;
    }
    .content{
        position:relative;
        top:40%;
        left:50%;
        transform:translate(-50%,-50%);
        background-color: #fff;
        width: 1000px;
        height: 400px;
        border:2px solid;
        border-radius:25px;
        padding: 20px;
        z-index: 999;
        text-align: left;
        box-shadow:0px 0px 0px  #ccc;

    }
</style>