/**//**//**//**//**//**//**//** 
*  
* @param d the delimiter 
* @param p the pattern of your date 
2006-06-25    style  
*/ 
String.prototype.toDate = function(style) { 
let y = this.substring(style.indexOf('y'),style.lastIndexOf('y')+1);//  
let M = this.substring(style.indexOf('M'),style.lastIndexOf('M')+1);//  
let d = this.substring(style.indexOf('d'),style.lastIndexOf('d')+1);//  
//let h = this.substring(style.indexOf('h'),style.lastIndexOf('h')+1);//  
//let m = this.substring(style.indexOf('m'),style.lastIndexOf('m')+1);//  
//let s = this.substring(style.indexOf('s'),style.lastIndexOf('s')+1);//  

//if(s == null ||s == "" || isNaN(s)) {s = new Date().getSeconds();} 
//if(m == null ||m == "" || isNaN(m)) {m = new Date().getMinutes();} 
//if(h == null ||h == "" || isNaN(h)) {h = new Date().getHours();} 
if(d == null ||d == "" || isNaN(d)) {d = new Date().getDate();} 
if(M == null ||M == "" || isNaN(M)) {M = new Date().getMonth()+1;} 
if(y == null ||y == "" || isNaN(y)) {y = new Date().getFullYear();} 
let dt ; 
eval ("dt = new Date('"+ y+"', '"+(M-1)+"','"+ d+"')"); 
return dt; 
} 

/**//**//**//**//**//**//**//** 
*  
* @param   d the delimiter 
* @param   p the pattern of your date 
* @author meizz 
*/ 
Date.prototype.format = function(style) { 
let o = { 
"M+" : this.getMonth() + 1, //month 
"d+" : this.getDate(),      //day 
"h+" : this.getHours(),     //hour 
"m+" : this.getMinutes(),   //minute 
"s+" : this.getSeconds(),   //second 
"w+" : "".charAt(this.getDay()),   //week 
"q+" : Math.floor((this.getMonth() + 3) / 3), //quarter 
"S" : this.getMilliseconds() //millisecond 
} 
if(/(y+)/.test(style)) { 
style = style.replace(RegExp.$1, 
(this.getFullYear() + "").substr(4 - RegExp.$1.length)); 
} 
for(let k in o){ 
if(new RegExp("("+ k +")").test(style)){ 
style = style.replace(RegExp.$1, 
RegExp.$1.length == 1 ? o[k] : 
("00" + o[k]).substr(("" + o[k]).length)); 
} 
} 
return style; 
} 

//2007-09-14  
Calendar.prototype.ReturnDate = function(dt) {
     
    if (this.dateControl != null){
        this.dateControl.value = dt;
        let event= document.createEvent("HTMLEvents");
        event.initEvent("input", false, true);
        this.dateControl.dispatchEvent(event);
    } 
    calendar1.hide(); 
    if(this.dateControl.onchange == null){
        return;
    } 
    // onchange  
    let ev = this.dateControl.onchange.toString(); // 
    ev = ev.substring(((ev.indexOf("ValidatorOnChange();")> 0) ? ev.indexOf("ValidatorOnChange();") + 20 : ev.indexOf("{") + 1),ev.lastIndexOf("}"));// ValidatorOnChange(); 
    let fun = new Function(ev);        // 
    this.dateControl.changeEvent = fun; 
    this.dateControl.changeEvent();// changeEvent  
} 

