// 
Calendar.prototype.bindYear = function() { 
let cy = this.calendarYear;//2006-12-01  
cy.length = 0; 
for (let i = this.beginYear; i <= this.endYear; i++){ 
cy.options[cy.length] = new Option(i + Calendar.language["year"][this.lang], i); 
} 
} 

// 
Calendar.prototype.bindMonth = function() { 
let cm = this.calendarMonth;//2006-12-01  
cm.length = 0; 
for (let i = 0; i < 12; i++){ 
cm.options[cm.length] = new Option(Calendar.language["months"][this.lang][i], i); 
} 
} 

// 
Calendar.prototype.bindHour = function() { 
let ch = this.calendarHour; 
if(ch.length > 0){return;}//2009-03-03  
//ch.length = 0; 
let h; 
for (let i = 0; i < 24; i++){ 
h = ("00" + i +"").substr(("" + i).length); 
ch.options[ch.length] = new Option(h, h); 
} 
} 

// 
Calendar.prototype.bindMinute = function() { 
let cM = this.calendarMinute; 
if(cM.length > 0){return;}//2009-03-03  
//cM.length = 0; 
let M; 
for (let i = 0; i < 60; i++){ 
M = ("00" + i +"").substr(("" + i).length); 
cM.options[cM.length] = new Option(M, M); 
} 
} 

// 
Calendar.prototype.bindSecond = function() { 
let cs = this.calendarSecond; 
if(cs.length > 0){return;}//2009-03-03  
//cs.length = 0; 
let s; 
for (let i = 0; i < 60; i++){ 
s = ("00" + i +"").substr(("" + i).length); 
cs.options[cs.length] = new Option(s, s); 
} 
} 

// 
Calendar.prototype.goPrevMonth = function(){ 
if (this.year == this.beginYear && this.month == 0){return;} 
this.month--; 
if (this.month == -1) { 
this.year--; 
this.month = 11; 
} 
this.date = new Date(this.year, this.month, 1); 
this.changeSelect(); 
this.bindData(); 
} 

// 
Calendar.prototype.goNextMonth = function(){ 
if (this.year == this.endYear && this.month == 11){return;} 
this.month++; 
if (this.month == 12) { 
this.year++; 
this.month = 0; 
} 
this.date = new Date(this.year, this.month, 1); 
this.changeSelect(); 
this.bindData(); 
} 

//SELECT 
Calendar.prototype.changeSelect = function() { 
let cy = this.calendarYear;//2006-12-01  
let cm = this.calendarMonth; 
//let ch = this.calendarHour; 
//let cM = this.calendarMinute; 
//let cs = this.calendarSecond; 
//2006-12-30 . 
cy[this.date.getFullYear()-this.beginYear].selected = true; 
cm[this.date.getMonth()].selected =true; 

//2009-03-03  
///ch[this.hour].selected =true; 
//cM[this.minute].selected =true; 
//cs[this.second].selected =true; 
} 

// 
Calendar.prototype.update = function (e){ 
this.year = e.calendarYear.options[e.calendarYear.selectedIndex].value;//2006-12-01  
this.month = e.calendarMonth.options[e.calendarMonth.selectedIndex].value; 
this.date = new Date(this.year, this.month, 1); 
//this.changeSelect(); 
this.bindData(); 
} 

// 
Calendar.prototype.bindData = function () { 
    calendar1 = this; 
    if(calendar1.DateMode >= pickMode["month"]){return;}//2008-01-29 
    // let dateArray = this.getMonthViewArray(this.date.getYear(), this.date.getMonth()); 
    //2006-12-30 . Firefox  
    let dateArray = this.getMonthViewArray(this.date.getFullYear(), this.date.getMonth()); 
    let tds = this.getElementById("calendarTable").getElementsByTagName("td"); 
    for(let i = 0; i < tds.length; i++) { 
        tds[i].style.backgroundColor = calendar1.colors["td_bg_out"]; 
        tds[i].onclick = function () {return;} 
        tds[i].onmouseover = function () {return;} 
        tds[i].onmouseout = function () {return;} 
        if (i > dateArray.length - 1) break; 
        tds[i].innerHTML = dateArray[i]; 
        if (dateArray[i] != "&nbsp;"){ 
            tds[i].bgColorTxt = "td_bg_out"; //2009-03-03  class 
            let  cur = new Date(); 
            tds[i].isToday = false; 
            if (cur.getFullYear() == calendar1.date.getFullYear() && cur.getMonth() == calendar1.date.getMonth() && cur.getDate() == dateArray[i]) { 
                // 
                tds[i].style.backgroundColor = calendar1.colors["cur_bg"]; 
                tds[i].bgColorTxt = "cur_bg"; 
                tds[i].isToday = true; 
            } 
            if(calendar1.dateControl != null ) 
            { 
                cur = calendar1.dateControl.value.toDate(calendar1.dateFormatStyle); 
                if (cur.getFullYear() == calendar1.date.getFullYear() && cur.getMonth() == calendar1.date.getMonth()&& cur.getDate() == dateArray[i]) { 
                    // 
                    calendar1.selectedDayTD = tds[i]; 
                    tds[i].style.backgroundColor = calendar1.colors["sel_bg"]; 
                    tds[i].bgColorTxt = "sel_bg"; 
                } 
            } 
            tds[i].onclick = function () { 
                if(calendar1.DateMode == pickMode["day"]) //2009-03-03  
                {   
                    
                    calendar1.ReturnDate(new Date(calendar1.date.getFullYear(), calendar1.date.getMonth(), this.innerHTML).format(calendar1.dateFormatStyle)); 
                }else{ 
                    if(calendar1.selectedDayTD != null) //2009-03-03  
                    { 
                    calendar1.selectedDayTD.style.backgroundColor =(calendar1.selectedDayTD.isToday)? calendar1.colors["cur_bg"] : calendar1.colors["td_bg_out"]; 
                    } 
                    this.style.backgroundColor = calendar1.colors["sel_bg"]; 
                    calendar1.day = this.innerHTML; 
                    calendar1.selectedDayTD = this; //2009-03-03  
                } 
            } 
            tds[i].style.cursor ="pointer"; //2007-08-06  
            tds[i].onmouseover = function () { 
                this.style.backgroundColor = calendar1.colors["td_bg_over"]; 
            } 
            tds[i].onmouseout = function () { 
                if(calendar1.selectedDayTD != this){ 
                    this.style.backgroundColor = calendar1.colors[this.bgColorTxt];
                } 
            } 
            tds[i].onblur = function () {calendar1.onblur();} 
        } 
    } 
} 

//() 
Calendar.prototype.getMonthViewArray = function (y, m) { 
let mvArray = []; 
let dayOfFirstDay = new Date(y, m, 1).getDay(); 
let daysOfMonth = new Date(y, m + 1, 0).getDate(); 
for (let i = 0; i < 42; i++) { 
mvArray[i] = "&nbsp;"; 
} 
for (let i = 0; i < daysOfMonth; i++){ 
mvArray[i + dayOfFirstDay] = i + 1; 
} 
return mvArray; 
} 

// document.getElementById(id)  from meizz tree source 
Calendar.prototype.getElementById = function(id){ 
if (typeof(id) != "string" || id == "") return null; 
if (document.getElementById) return document.getElementById(id); 
if (document.all) return document.all(id); 
try {return eval(id);} catch(e){ return null;} 
} 

// object.getElementsByTagName(tagName) 
Calendar.prototype.getElementsByTagName = function(object, tagName){ 
if (document.getElementsByTagName) return document.getElementsByTagName(tagName); 
if (document.all) return document.all.tags(tagName); 
} 

//HTML 
Calendar.prototype.getAbsPoint = function (e){ 
let x = e.offsetLeft; 
let y = e.offsetTop; 
// eslint-disable-next-line no-cond-assign
while(e = e.offsetParent){ 
x += e.offsetLeft; 
y += e.offsetTop; 
} 
return {"x": x, "y": y}; 
} 

// 
Calendar.prototype.show = function (dateObj, popControl) { 

if (dateObj == null){ 
throw new Error("arguments[0] is necessary") 
} 
this.dateControl = dateObj; 
let now = new Date(); 
this.date = (dateObj.value.length > 0) ? new Date(dateObj.value.toDate(this.dateFormatStyle)) : now.format(this.dateFormatStyle).toDate(this.dateFormatStyle) ;//2008-01-29   dateFormatStyle 

if(this.panel.innerHTML==""||cal.dateFormatStyleOld != cal.dateFormatStyle)//2008-01-29 2009-03-03  
{ 
this.draw(); 
this.bindYear(); 
this.bindMonth(); 
//this.bindHour(); 
//this.bindMinute(); 
//this.bindSecond(); 
} 
this.year = this.date.getFullYear(); 
this.month = this.date.getMonth(); 
this.day = this.date.getDate(); 
//this.hour = this.date.getHours(); 
//this.minute = this.date.getMinutes(); 
//this.second = this.date.getSeconds(); 
this.changeSelect(); 
this.bindData(); 

if (popControl == null){ 
popControl = dateObj; 
} 
let xy = this.getAbsPoint(popControl); 
//this.panel.style.left = xy.x + "px"; 
//this.panel.style.top = (xy.y + dateObj.offsetHeight) + "px"; 
this.panel.style.left = (xy.x + leftX)+ "px"; // 2007-02-11    
this.panel.style.top = (xy.y + topY + dateObj.offsetHeight) + "px"; 

// 2006-06-25    visibility  display //this.setDisplayStyle("select", "hidden"); 
//this.panel.style.visibility = "visible"; 
//this.container.style.visibility = "visible"; 
this.panel.style.display = ""; 
this.container.style.display = ""; 

if( !this.dateControl.isTransEvent) 
{ 
this.dateControl.isTransEvent = true; 
/*  ReturnDate  
this.dateControl.changeEvent = this.dateControl.onchange;// onchange  
this.dateControl.onchange = function() 
{if(typeof(this.changeEvent) =='function'){this.changeEvent();}}*/ 
if(this.dateControl.onblur != null){ 
this.dateControl.blurEvent = this.dateControl.onblur;}//2007-09-14  onblur  
this.dateControl.onblur = function() 
{
    calendar1.onblur();if(typeof(this.blurEvent) =='function'){this.blurEvent();} 
} 
} 

this.container.onmouseover = function(){isFocus=true;} 
this.container.onmouseout = function(){isFocus=false;} 
} 

// 
Calendar.prototype.hide = function() { 
//this.setDisplayStyle("select", "visible"); 
//this.panel.style.visibility = "hidden"; 
//this.container.style.visibility = "hidden"; 
this.panel.style.display = "none"; 
this.container.style.display = "none"; 
isFocus=false; 
} 

//   2006-06-25  
Calendar.prototype.onblur = function() { 
if(!isFocus){this.hide();} 
} 

