<template>
    <span id="bdwid">
  <!--  -->
  <table id="Tail" class="common" cellpadding="3" cellspacing="0">
    <tr>
      <input type="hidden" name="Tail_Flag" description="">
      <td id="tdJudicalScopeTitle" class="page" colspan=4>
        <label id="trJudicalScope">       
              <input type="text" name="judicalScope" ref="judicalScope" class="common3" style="width:27%" maxlength="40" 
               description=""  v-model="judicalScope" @blur=$uiCommon.checkLength($event)>
        </label>
        <label id="trArgueSolution">
          <input type="hidden" name="flag" description="" :value='flag'>
          <select name="argueSolution"  ref='argueSolution' class="common3" style="width:8%" description="" v-model="argueSolution"
            @change="changeArgueSolution();">
            <option value="1" selected></option>
            <option value="2"></option>
            <option value="3"></option>
          </select>
          <span id="spanArbitBoardName" v-show="argueShow">
            <select  class='common3'  name="arbitBoardName" ref="arbitBoardName" v-model='arbitBoardName'>
              <option :value="arbitraryBoard.codeCode"  v-for=" arbitraryBoard,i in arbitraryBoardList" :key="i" >
              {{arbitraryBoard.codeName}}</option>
            </select>&nbsp;<img :src="imgUrl">
          </span>
          <span id="spanOther" style="display:none">
            <input type="text" name="OtherText" style="width:25%" class="common3" maxlength="60" description="">&nbsp;
            <img  :src="imgUrl">
          </span>
        </label>    
      </td>
    </tr>
    <tr>
    <td colspan=4>
      <label id="tdArbitBoardAddress" style="display:none" >
        <input type="text" name="ArbitBoardAddress" class="common3" style="width:28%" description="">
        <img :src="imgUrl">
     </label>
     </td>
    </tr>
    <tr id="trOperatorCode" style="display:none">
      <td id="tdOperatorCodeTitle" class="title"></td>
      <td id="tdOperatorCodeInput" class="input">
        <input type="text" name="OperatorCode" class="readonly3" readonly style="width:28%" description="">
        <input type="text" name="OperatorName" class="readonly3" readonly style="width:56%" description="">
      </td>
      <td  class="title"></td>
      <td id="tdOperateDateInput" class="input">
        <input type="text" name="InputDate" class="readonly3" readonly description="">
        <input type="hidden" name="InputHour" description="">
      </td>
    </tr>
    <tr id="trUpdaterCode" style="display:none">
      <td id="tdUpdaterCodeTitle" class="title"></td>
      <td id="tdUpdaterCodeInput" class="input">
        <input type="text" name="UpdaterCode" class="readonly3" readonly style="width:28%" description="">
        <input type="text" name="UpdaterName" class="readonly3" readonly style="width:56%" description="">
      </td>
      <td id="tdUpdateDateTitle" class="title"></td>
      <td id="tdUpdateDateInput" class="input">
        <input type="text" name="UpdateDate" class="readonly3" readonly description="">
        <input type="hidden" name="UpdateHour" description="">
      </td>
    </tr>
    <tr id="trUnderWriteCode" style="display:none">
      <td id="tdUnderWriteTitle" class="title"></td>
      <td id="tdUnderWriteInput" class="input">
        <input type="text" name="UnderWriteCode" class="readonly3" readonly style="width:28%" description="">
        <input type="text" name="UnderWriteName" class="readonly3" readonly style="width:56%" description="">
      </td>
      <td id="tdUnderWriteEndDateTitle" class="title"></td>
      <td id="tdUnderWriteEndDateInput" class="input">
        <input type="text" name="UnderWriteEndDate" class="readonly3" readonly description="">
      </td>
    </tr>
    <tr id="trEndorseOperator" style="display:none">
      <td id="tdOperatorCodePTitle" class="title"></td>
      <td id="tdOperatorCodePInput" class="input">
        <input type="text" name="OperatorCodeP" class="readonly3" readonly style="width:28%" description="">
        <input type="text" name="OperatorNameP" class="readonly3" readonly style="width:56%" description="">
      </td>
      <td class="title">&nbsp;</td>
      <td class="input">&nbsp;</td>
    </tr>
    <tr id="trMainRemark">
      <td id="tdMainRemarkTitle" class="title" >
        <input  name="button_MainRemark_Open_Context" class="button" type="button" v-model="RemarkButton" alt="" src="@/assets/images/butInputCoins.gif"
          @click="dialogFormVisible = true" >
        <span id="span_MainRemark_Context" style="width:520;display:none;position:absolute;background-color:C0C0C0;" v-show="dialogFormVisible">
          <table class=list cellpadding="3" cellspacing="1">
            <tr class=common>
              <td >
                <textarea class='common' name='remark' ref='remark' v-model="remark" rows="3" cols="80" maxLength="1000" description=""></textarea>
              </td>
            </tr>
            <tr class=common>
              <td>
                <input class="button" type="button" name="button_LadingNo_Close_Context" alt="" value=" " 
                src="@/assets/images/butOk.gif" @click="submitRemark()">
              </td>
            </tr>
          </table>
        </span>
      </td>
      <td class=input colspan=3>
        <input name="MainRemark_Show" class="readonly" readonly description="" v-model="MainRemark_Show">
      </td>
    </tr>
    <tr id="trArgueSolutionArbitBoardName"></tr>
  </table>
 
</span>
</template>

<script>
export default{
   name:'bdwid',
    data() {
      return {
        imgUrl:require('@/assets/images/markMustInput.jpg'),
        dialogFormVisible: false,
        RemarkButton:'',
        remark:'',
        flag:'',//  main
        MainRemark_Show:'',
        judicalScope:"",
        argueSolution:'1',
        arbitraryBoardList:[],
        argueShow:false,
        arbitBoardName:'A01'
      };
    },
  
   methods:{
      getUrl(){ },
      submitRemark(){
          this.dialogFormVisible=!this.dialogFormVisible;
          this.RemarkButton='';
          this.MainRemark_Show=this.remark;
      },
      changeArgueSolution(){
        this.argueSolution=='2'?this.argueShow=true:this.argueShow=false
      },
      initSelected(data){
          this.arbitraryBoardList=data.arbitraryBoardList
      },
      initMainTailData(obj){
        this.judicalScope=obj.mainInfoVo.judicalScope
        this.argueSolution=obj.mainInfoVo.argueSolution
        if(obj.mainInfoVo.remark!=null){
           this.remark=obj.mainInfoVo.remark
        }
        if(obj.mainInfoVo.arbitboardName!=null){
          this.arbitBoardName=obj.mainInfoVo.arbitboardName

        }
        if(this.$store.state.bizType=='ENDORSE'){
          this.$nextTick(()=>{
            this.$refs.judicalScope.title=this.judicalScope
            this.$refs.argueSolution.title=this.argueSolution
            this.$refs.arbitBoardName.title=this.arbitBoardName
            this.$refs.remark.title=this.remark
            this.$uiCommon.addevent(this,'remark')
            this.$uiCommon.addevent(this,'judicalScope')
            this.$uiCommon.addevent(this,'argueSolution')
            this.$uiCommon.addevent(this,'arbitBoardName')
          })
        }
      }
   }
}
</script>

